.item-container {
    width: 100%;
    padding: 10px 0px 10px 0px;
    display: inline-block;
    text-align: left;
    position: relative;
    background-color: white;
    font-weight: 300;
}

.item-container:hover{
    background:#eee;
}

.completed-check-box {
    height: 30px;
    width: 30px;
    border-radius: 50%;
    border: solid 1px #c9c9c9;
    display: inline-block;
    margin-left: 10px;
    vertical-align: middle;
}

.title-container {
    padding: 0px 20px 0px 20px;
    display: inline-block;
    width: 70%;
    font-size: 20px;
    position: relative;
    vertical-align: middle;
}

.title {
    margin: 0;
    color: #606060;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 0%);
}

.title-crossed {
    margin: 0;
    color: #606060;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 0%);
    text-decoration: line-through;
    font-style: italic;
}

.edit-title {
    margin-left: 5px;
    border: 0;
    height: 50px;
    padding-left: 8px;
    color: #606060;
    position: relative;
    /*
    top: 50%;
    left: 50%;
    transform: translate(-50%, 0%);*/
    outline-color: gray;
    font-size: 20px;
    font-weight: 300;
    display: block;
}

.delete-btn {
    display: inline-block;
    padding: 0px 30px 0px 10px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(0%, -50%);
    cursor: pointer;
}

.save-btn{
    display: inline-block;
    padding: 0px 30px 0px 10px;
    position: absolute;
    top: 50%;
    right: 0%;
    transform: translate(0%, -50%);
    cursor: pointer;
    color: #237d27; 
    font-size:2em;
}

.details input, .details p{
    text-indent:15px
}
.details-btn{
    display: inline-block;
    margin: 0px 50px 0px 10px;
    position: absolute;
    top: 50%;
    right: 30px;
    transform: translate(0%, -50%);
    cursor: pointer;
    color:teal;
}
.check-icon {
    font-size: 20px;
    position: relative;
    top: 5px;
    left: 5px;
    color: green;
}

.delete-icon {
    color: #c43c3c;
}

.details{
    transition:all .3s cubic-bezier(.87,-.41,.19,1.44);
    background:#ddd;
    position: relative;
    text-align:left;
    
    
}

.details.hidden {
    height:0;
    opacity:0;
}
.details.show {
    height: 200px;
    opacity:1;
    padding: 20px 20px;
    color:black;    
}
.details p{
    color:#333;    

}
.details a{
    text-align:center;
    position: absolute;
    bottom:20px;
}

.details.show a{
    display:block
}

.details.hidden a{
    display:none
}
